body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
  background-image: url(/blockchain-fitness-voting/static/media/bg-1.d85eef01.jpeg);
  color: rgb(255, 255, 255);
  height: 100vh;
}

.App-logo {
  height: 30px;
}

.App-header {
  background-color: #d9edf735;
  height: 110px;
  padding: 20px;
  color: rgb(255, 255, 255);
  margin-bottom: 20px;
}

.App-footer {
  background-color: #d9edf735;
  height: 110px;
  padding: 20px;
  color: rgb(255, 255, 255);
  margin-bottom: 20px;
}

.App-title {
  font-size: 3em;
  margin-top: 0px;
  font-style: italic;
  font-family: "cursive";
  font-size: "large";
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

